(function() {
  this.App.controller('UpdateClientsController', ["$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeSubTenants", "MnoErrorsHandler", "MnoeOrganizations", "subTenant", function($filter, $stateParams, $log, $uibModalInstance, toastr, MnoeSubTenants, MnoErrorsHandler, MnoeOrganizations, subTenant) {
    'ngInject';
    var displayCurrentState, fetchOrganizations, i, id, len, ref, setAllOrganizationsList, setSearchOrganizationsList, vm;
    vm = this;
    vm.selectedOrganizations = {};
    ref = subTenant.client_ids;
    for (i = 0, len = ref.length; i < len; i++) {
      id = ref[i];
      vm.selectedOrganizations[id] = true;
    }
    vm.organizations = {
      search: '',
      nbItems: 10,
      page: 1,
      pageChangedCb: function(nbItems, page) {
        var offset;
        vm.organizations.nbItems = nbItems;
        vm.organizations.page = page;
        offset = (page - 1) * nbItems;
        return fetchOrganizations(nbItems, offset);
      }
    };
    fetchOrganizations = function(limit, offset, sort) {
      if (sort == null) {
        sort = 'name';
      }
      vm.organizations.loading = true;
      return MnoeOrganizations.list(limit, offset, sort).then(function(response) {
        vm.organizations.totalItems = response.headers('x-total-count');
        return vm.organizations.list = response.data;
      }).then(function() {
        return vm.organizations.loading = false;
      });
    };
    displayCurrentState = function() {
      setAllOrganizationsList();
      return fetchOrganizations(vm.organizations.nbItems, 0);
    };
    setAllOrganizationsList = function() {
      return vm.organizations.widgetTitle = 'All organisations';
    };
    vm.searchChange = function() {
      if (vm.organizations.search.length >= 3) {
        vm.searchMode = true;
        return setSearchOrganizationsList(vm.organizations.search);
      } else if (vm.searchMode) {
        vm.searchMode = false;
        return displayCurrentState();
      }
    };
    setSearchOrganizationsList = function(search) {
      var terms;
      vm.organizations.loading = true;
      vm.organizations.widgetTitle = 'Search result';
      search = vm.organizations.search.toLowerCase();
      terms = {
        'name.like': "%" + search + "%"
      };
      return MnoeOrganizations.search(terms).then(function(response) {
        vm.organizations.totalItems = response.headers('x-total-count');
        return vm.organizations.list = $filter('orderBy')(response.data, 'name');
      })["finally"](function() {
        return vm.organizations.loading = false;
      });
    };
    displayCurrentState();
    vm.onSubmit = function() {
      var orgId, val;
      vm.isLoading = true;
      subTenant.client_ids = (function() {
        var ref1, results;
        ref1 = vm.selectedOrganizations;
        results = [];
        for (orgId in ref1) {
          val = ref1[orgId];
          if (val) {
            results.push(orgId);
          }
        }
        return results;
      })();
      return MnoeSubTenants.update(subTenant).then(function(result) {
        toastr.success("mnoe_admin_panel.dashboard.sub_tenant.select_clients.modal.toastr_success", {
          extraData: {
            sub_tenant_name: subTenant.name
          }
        });
        return $uibModalInstance.close(result.data.sub_tenant.clients);
      }, function(error) {
        toastr.error("mnoe_admin_panel.dashboard.sub_tenant.select_clients.modal.toastr_error", {
          extraData: {
            sub_tenant_name: subTenant.name
          }
        });
        return $log.error("An error occurred while updating account managers of " + subTenant.name + ".", error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);
