(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  this.App.service('MnoeSubTenants', ["MnoeAdminApiSvc", "MnoeObservables", "OBS_KEYS", function(MnoeAdminApiSvc, MnoeObservables, OBS_KEYS) {
    var filterElements;
    this.list = function(limit, offset, sort) {
      return MnoeAdminApiSvc.all("sub_tenants").getList({
        order_by: sort,
        limit: limit,
        offset: offset
      }).then(function(response) {
        return response;
      });
    };
    this.get = function(id) {
      return MnoeAdminApiSvc.one('sub_tenants', id).get();
    };
    this.create = function(subTenant) {
      var promise;
      return promise = MnoeAdminApiSvc.all('sub_tenants').post(subTenant).then(function(response) {
        MnoeObservables.notifyObservers(OBS_KEYS.subTenantAdded, promise);
        return response;
      }, function(error) {
        return $log.error('Error while creating subTenant: ' + subTenant, error);
      });
    };
    filterElements = function(validate, filter) {
      var filtered, key, value;
      filtered = {};
      for (key in validate) {
        value = validate[key];
        if (indexOf.call(filter, key) >= 0) {
          filtered[key] = value;
        }
      }
      return filtered;
    };
    this.update = function(subTenant) {
      var data, promise;
      data = {
        sub_tenant: filterElements(subTenant, ["name", "client_ids", "account_manager_ids"])
      };
      return promise = MnoeAdminApiSvc.one('sub_tenants', subTenant.id).patch(data).then(function(response) {
        MnoeObservables.notifyObservers(OBS_KEYS.subTenantChanged, promise);
        return response;
      }, function(error) {
        return $log.error('Error while updating subTenant: ' + id, error);
      });
    };
    this["delete"] = function(id) {
      var promise;
      return promise = MnoeAdminApiSvc.one('sub_tenants', id).doDELETE().then(function() {
        return MnoeObservables.notifyObservers(OBS_KEYS.subTenantChanged, promise);
      }, function(error) {
        return $log.error('Error while deleting subTenant: ' + id, error);
      });
    };
    return this;
  }]);

}).call(this);
