(function() {
  this.App.controller('UpdateAccountManagersController', ["$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeSubTenants", "MnoeUsers", "MnoErrorsHandler", "ADMIN_ROLES", "subTenant", function($filter, $stateParams, $log, $uibModalInstance, toastr, MnoeSubTenants, MnoeUsers, MnoErrorsHandler, ADMIN_ROLES, subTenant) {
    'ngInject';
    var fetchStaffs, updateSearch, updateSort, vm;
    vm = this;
    vm.selectedUsers = {};
    _.each(subTenant.account_manager_ids, function(id) {
      return vm.selectedUsers[id] = true;
    });
    vm = this;
    vm.staff_roles = ADMIN_ROLES;
    vm.getAdminRoleLabel = function(admin_role) {
      return _.find(vm.staff_roles, function(role) {
        return role.value === admin_role;
      }).label;
    };
    vm.callServer = function(tableState) {
      var search, sort;
      sort = updateSort(tableState.sort);
      search = updateSearch(tableState.search);
      return fetchStaffs(vm.staff.nbItems, vm.staff.offset, sort, search);
    };
    updateSort = function(sortState) {
      var sort;
      if (sortState == null) {
        sortState = {};
      }
      sort = "surname";
      if (sortState.predicate) {
        sort = sortState.predicate;
        if (sortState.reverse) {
          sort += ".desc";
        } else {
          sort += ".asc";
        }
      }
      vm.staff.sort = sort;
      return sort;
    };
    updateSearch = function(searchingState) {
      var attr, ref, search, value;
      if (searchingState == null) {
        searchingState = {};
      }
      search = {};
      if (searchingState.predicateObject) {
        ref = searchingState.predicateObject;
        for (attr in ref) {
          value = ref[attr];
          if (attr === "admin_role") {
            search['where[admin_role.in][]'] = [value];
          } else {
            search['where[' + attr + '.like]'] = value + '%';
          }
        }
      }
      vm.staff.search = search;
      return search;
    };
    vm.state = vm.view;
    vm.staff = {
      search: {},
      sort: "surname",
      nbItems: 10,
      page: 1,
      pageChangedCb: function(nbItems, page) {
        var offset;
        vm.staff.nbItems = nbItems;
        vm.staff.page = page;
        offset = (page - 1) * nbItems;
        return fetchStaffs(nbItems, offset);
      }
    };
    fetchStaffs = function(limit, offset, sort, search) {
      if (sort == null) {
        sort = vm.staff.sort;
      }
      if (search == null) {
        search = vm.staff.search;
      }
      vm.staff.loading = true;
      return MnoeUsers.staffs(limit, offset, sort, search).then(function(response) {
        vm.staff.totalItems = response.headers('x-total-count');
        return vm.listOfStaff = response.data;
      })["finally"](function() {
        return vm.staff.loading = false;
      });
    };
    vm.onSubmit = function() {
      var orgId, val;
      vm.isLoading = true;
      subTenant.account_manager_ids = (function() {
        var ref, results;
        ref = vm.selectedUsers;
        results = [];
        for (orgId in ref) {
          val = ref[orgId];
          if (val) {
            results.push(orgId);
          }
        }
        return results;
      })();
      return MnoeSubTenants.update(subTenant).then(function(result) {
        toastr.success("mnoe_admin_panel.dashboard.sub_tenant.select_account_managers.modal.toastr_success", {
          extraData: {
            sub_tenant_name: subTenant.name
          }
        });
        return $uibModalInstance.close(result.data.sub_tenant.account_managers);
      }, function(error) {
        toastr.error("mnoe_admin_panel.dashboard.sub_tenant.select_account_managers.modal.toastr_error", {
          extraData: {
            sub_tenant_name: subTenant.name
          }
        });
        return $log.error("An error occurred while updating account managers of " + subTenant.name + ".", error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);
