(function() {
  this.App.controller('CreateSubTenantController', ["$filter", "$stateParams", "$log", "$uibModalInstance", "toastr", "MnoeSubTenants", "MnoErrorsHandler", function($filter, $stateParams, $log, $uibModalInstance, toastr, MnoeSubTenants, MnoErrorsHandler) {
    'ngInject';
    var vm;
    vm = this;
    vm.onSubmit = function() {
      vm.isLoading = true;
      return MnoeSubTenants.create(vm.subTenant).then(function(success) {
        toastr.success("mnoe_admin_panel.dashboard.sub_tenants.create_sub_tenant.toastr_success", {
          extraData: {
            sub_tenant_name: vm.subTenant.name
          }
        });
        return $uibModalInstance.close(success.data);
      }, function(error) {
        toastr.error("mnoe_admin_panel.dashboard.sub_tenants.create_sub_tenant.toastr_error", {
          extraData: {
            sub_tenant_name: vm.subTenant.name
          }
        });
        return $log.error("An error occurred:", error);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    vm.onCancel = function() {
      return $uibModalInstance.dismiss('cancel');
    };
  }]);

}).call(this);
