(function() {
  this.App.controller('SubTenantsController', ["$stateParams", "$window", "$uibModal", "MnoeSubTenants", function($stateParams, $window, $uibModal, MnoeSubTenants) {
    'ngInject';
    var vm;
    vm = this;
    vm.createModal = function() {
      return $uibModal.open({
        templateUrl: 'app/views/sub-tenants/create-sub-tenant-modal/create-sub-tenant.html',
        controller: 'CreateSubTenantController',
        controllerAs: 'vm'
      });
    };
    MnoeSubTenants.get($stateParams.subTenantId).then(function(response) {
      return vm.subTenant = response.data;
    });
  }]);

}).call(this);
