(function() {
  this.App.directive('mnoeQuestionsList', ["$filter", "$log", "$uibModal", "MnoeReviews", function($filter, $log, $uibModal, MnoeReviews) {
    return {
      restric: 'E',
      scope: {},
      templateUrl: 'app/components/mnoe-questions-list/mnoe-questions-list.html',
      link: function(scope) {
        var fetchQuestions;
        scope.editmode = [];
        scope.listOfQuestions = [];
        scope.statuses = [
          {
            value: 'approved',
            label: 'mnoe_admin_panel.dashboard.questions_list.status_label.approved'
          }, {
            value: 'rejected',
            label: 'mnoe_admin_panel.dashboard.questions_list.status_label.rejected'
          }
        ];
        scope.openCommentModal = function() {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-modals/comment-modal.html',
            controller: 'CommentModal'
          });
        };
        scope.openEditModal = function(question) {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-modals/question-edit-modal.html',
            controller: 'CommentEditModal',
            resolve: {
              review: question
            }
          }).result.then(function(question) {
            return MnoeReviews.updateDescription(question);
          });
        };
        scope.openEditAnswerModal = function(answer) {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-modals/answer-edit-modal.html',
            controller: 'CommentEditModal',
            resolve: {
              review: answer
            }
          }).result.then(function(answer) {
            return MnoeReviews.updateDescription(answer);
          });
        };
        scope.openQuestionReplyModal = function(question) {
          return $uibModal.open({
            templateUrl: 'app/components/mnoe-modals/question-reply-modal.html',
            controller: 'QuestionReplyModal'
          }).result.then(function(replyText) {
            return MnoeReviews.replyQuestion(question.id, replyText).then(function(response) {
              return question.answers.unshift(response.data.app_answer);
            });
          });
        };
        fetchQuestions = function() {
          return MnoeReviews.listQuestions().then(function(response) {
            return scope.listOfQuestions = response.data;
          });
        };
        scope.update = function(question) {
          return MnoeReviews.updateRating(question).then(function(response) {
            return delete scope.editmode[question.id];
          });
        };
        fetchQuestions();
      }
    };
  }]);

}).call(this);
