(function() {
  this.App.controller('StaffsController', ["$filter", "$stateParams", "$uibModal", function($filter, $stateParams, $uibModal) {
    'ngInject';
    var vm;
    vm = this;
    vm.staff = {
      createModal: function() {
        return $uibModal.open({
          templateUrl: 'app/views/staffs/create-staff-modal/create-staff.html',
          controller: 'CreateStaffController',
          controllerAs: 'vm'
        });
      }
    };
    vm.displayed = [];
    vm.callServer = function() {
      return vm.isLoading = false;
    };
    return vm;
  }]);

}).call(this);
