(function() {
  this.App.component('mnoeSubTenantsList', {
    templateUrl: 'app/components/mnoe-sub-tenants-list/mnoe-sub-tenants-list.html',
    bindings: {
      view: '@'
    },
    controller: ["$filter", "$log", "toastr", "MnoeSubTenants", "MnoConfirm", "MnoeObservables", "OBS_KEYS", function($filter, $log, toastr, MnoeSubTenants, MnoConfirm, MnoeObservables, OBS_KEYS) {
      var fetchSubTenants, updateSort, vm;
      vm = this;
      vm.listOfSubTenant = [];
      vm.callServer = function(tableState) {
        var sort;
        sort = updateSort(tableState.sort);
        return fetchSubTenants(vm.subTenant.nbItems, vm.subTenant.offset, sort);
      };
      updateSort = function(sortState) {
        var sort;
        if (sortState == null) {
          sortState = {};
        }
        sort = "name";
        if (sortState.predicate) {
          sort = sortState.predicate;
          if (sortState.reverse) {
            sort += ".desc";
          } else {
            sort += ".asc";
          }
        }
        vm.subTenant.sort = sort;
        return sort;
      };
      vm.state = vm.view;
      vm.subTenant = {
        editmode: [],
        search: {},
        sort: "name",
        nbItems: 10,
        page: 1,
        pageChangedCb: function(nbItems, page) {
          var offset;
          vm.subTenant.nbItems = nbItems;
          vm.subTenant.page = page;
          offset = (page - 1) * nbItems;
          return fetchSubTenants(nbItems, offset);
        },
        update: function(subTenant) {
          return MnoeSubTenants.update(subTenant).then(function() {
            toastr.success('mnoe_admin_panel.dashboard.sub_tenants.update_sub_tenant.toastr_success', {
              extraData: {
                sub_tenant_name: subTenant.name
              }
            });
            return delete vm.subTenant.editmode[subTenant.id];
          }, function(error) {
            $log.error('Error while updating subTenant', error);
            return toastr.error('mnoe_admin_panel.dashboard.sub_tenants.update_sub_tenant.toastr_error');
          });
        },
        remove: function(subTenant) {
          var modalOptions;
          modalOptions = {
            closeButtonText: 'mnoe_admin_panel.dashboard.sub_tenants.remove_sub_tenant.cancel',
            actionButtonText: 'mnoe_admin_panel.dashboard.sub_tenants.remove_sub_tenant.delete',
            headerText: 'mnoe_admin_panel.dashboard.sub_tenants.remove_sub_tenant.proceed',
            headerTextExtraData: {
              sub_tenant_name: subTenant.name
            },
            bodyText: 'mnoe_admin_panel.dashboard.sub_tenants.modal.remove_sub_tenant.perform'
          };
          return MnoConfirm.showModal(modalOptions).then(function() {
            MnoeSubTenants["delete"](subTenant.id).then(function() {
              return toastr.success('mnoe_admin_panel.dashboard.sub_tenants.remove_sub_tenant.toastr_success', {
                extraData: {
                  sub_tenant_name: subTenant.name
                }
              });
            });
            return function(error) {
              $log.error('Error while deleting subTenant', error);
              return toastr.error('mnoe_admin_panel.dashboard.sub_tenants.remove_sub_tenant.toastr_success');
            };
          });
        }
      };
      fetchSubTenants = function(limit, offset, sort) {
        if (sort == null) {
          sort = vm.subTenant.sort;
        }
        vm.subTenant.loading = true;
        return MnoeSubTenants.list(limit, offset, sort).then(function(response) {
          vm.subTenant.totalItems = response.headers('x-total-count');
          return vm.listOfSubTenant = response.data;
        })["finally"](function() {
          return vm.subTenant.loading = false;
        });
      };
      MnoeObservables.registerCb(OBS_KEYS.subTenantAdded, function() {
        return fetchSubTenants(vm.subTenant.nbItems, vm.subTenant.offset);
      });
      MnoeObservables.registerCb(OBS_KEYS.subTenantChanged, function() {
        return fetchSubTenants(vm.subTenant.nbItems, vm.subTenant.offset);
      });
    }]
  });

}).call(this);
