(function() {
  this.App.controller('SubTenantController', ["$stateParams", "$window", "$uibModal", "MnoeSubTenants", function($stateParams, $window, $uibModal, MnoeSubTenants) {
    'ngInject';
    var vm;
    vm = this;
    MnoeSubTenants.get($stateParams.subTenantId).then(function(response) {
      return vm.subTenant = response.data;
    });
    vm.updateClientsModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/sub-tenant/update-clients-modal/update-clients.html',
        controller: 'UpdateClientsController',
        controllerAs: 'vm',
        resolve: {
          subTenant: function() {
            return vm.subTenant;
          }
        }
      });
      return modalInstance.result.then(function(clients) {
        return vm.subTenant.clients = clients;
      });
    };
    vm.updateAccountManagerModal = function() {
      var modalInstance;
      modalInstance = $uibModal.open({
        templateUrl: 'app/views/sub-tenant/update-account-managers-modal/update-account-managers.html',
        controller: 'UpdateAccountManagersController',
        controllerAs: 'vm',
        resolve: {
          subTenant: function() {
            return vm.subTenant;
          }
        }
      });
      return modalInstance.result.then(function(account_managers) {
        vm.subTenant.account_managers = account_managers;
        return function() {};
      });
    };
  }]);

}).call(this);
